import unitag from '../../components/uni-tag/uni-tag.vue';
import uniSection from '../../components/uni-section/uni-section.vue';
import { request } from '../../utils/request.js';
export default {
	components: { unitag, uniSection },
	data() {
		return {
			// 总收益
			all: '0',
			// 前日收益
			lasmo: '0',
			// 昨日收益
			yester: '0',
			// 可提现金额
			wimoney: '0',
			// 提现模态框显示隐藏
			showMonmodal: false,
			// 有无提现记录 0有 1无
			haslist: 1,
			// token
			token: '',
			// 要体现的金额
			form: {
				cashMoney: ''
			},
			// 提现记录
			widthdrawList: [],
			// 提现记录页码
			page: 1,
			// 是否有上一页
			hasprepage: true,
			// 是否有下一页
			hasNextpage: true
		};
	},
	onShow() {
		this.getToken();
		// 调用获取收益信息的函数
		this.getWithdraw();
		// 获取经纪人提现记录
		this.getWithdrawList();
	},
	methods: {
		// 获取经纪人提现记录
		getWithdrawList() {
			const that = this;
			request({
				url: '/api/user/withdrawList',
				data: { page: that.page }
			}).then(res => {
				if (res.data.data.lists) {
					that.haslist = 0;
				}
				console.log(res.data.data);
				that.widthdrawList = res.data.data.lists;
				if (res.data.data.has_next == 0) {
					this.hasNextpage = true;
				} else {
					this.hasNextpage = false;
				}
				if (this.page > 1) {
					this.hasprepage = false;
				} else {
					this.hasprepage = true;
				}
			});
		},
		// 上一页列表
		getPrepage() {
			this.page = this.page - 1;
			this.getWithdrawList();
		},
		// 下一页列表
		getNextpage() {
			this.page = this.page + 1;
			this.getWithdrawList();
		},
		// 获取token
		getToken() {
			this.token = uni.getStorageSync('token');
		},
		getWithdraw() {
			// 获取收益信息，更新数据，绑定数据
			const that = this;
			request({
				url: '/api/user/cashShow',
				method: 'POST'
			}).then(res => {
				that.all = res.data.data.total_money;
				that.lasmo = res.data.data.before_yester_money;
				that.yester = res.data.data.yester_money;
				that.wimoney = res.data.data.able_money;
			});
		},
		getMoney() {
			this.showMonmodal = true;
		},
		closeModal() {
			this.showMonmodal = false;
		},
		// 关于模态框的函数
		withdraw() {
			const that = this;
			if(that.form.cashMoney <= that.wimoney){
				request({
					url: '/api/user/applyWithdraw',
					method: 'POST',
					data: {
						money: that.form.cashMoney
					}
				}).then(res => {
					if (res.code !== 1) {
						console.log(res.data.msg);
						uni.showToast({
							title:res.data.msg,
							icon:"none"
						})
						that.form.cashMoney = ''
						this.getWithdraw();
						this.getWithdrawList();
					} else {
						console.log(res.data.msg);
						that.form.cashMoney = ''
						uni.showToast({
							title:res.data.msg,
							icon:'success'
						})
						this.getWithdraw();
						this.getWithdrawList();
					}
				});
			}else{
				uni.showToast({
					title:"超出可提现金额，请重新填写",
					icon:"none"
				})
				setTimeout(()=>{
					this.showMonmodal = true
				},2000)
			}
			
		}
	}
};
